            pageFlag : '',
			tableName: '',
			registerForm: {},
			forgetForm: {},
			rules: {},
			requiredRules: {},
            yonghuxingbieOptions: [],
		}
    },
	mounted() {
		if(this.$route.query.pageFlag=='register'){
			this.tableName = this.$route.query.role;
			if(this.tableName=='yonghu'){
				this.registerForm = {
					yonghuzhanghao: '',
					yonghuxingming: '',
					mima: '',
					mima2: '',
					xingbie: '',
					lianxidianhua: '',
					touxiang: '',
					money: '',
				}
			}
			if(this.tableName=='yuangong'){
				this.registerForm = {
					yuangonggonghao: '',
					yuangongxingming: '',
					mima: '',
					mima2: '',
					shoujihao: '',
					gangwei: '',
					zhiwei: '',
					touxiang: '',
					money: '',
				}
			}
			if ('yonghu' == this.tableName) {
				this.rules.yonghuzhanghao = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.yonghuzhanghao = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('yonghu' == this.tableName) {
				this.rules.yonghuxingming = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.yonghuxingming = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('yonghu' == this.tableName) {
				this.rules.mima = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.mima = [{ required: true, message: '', trigger: 'blur' }]
			}
			this.yonghuxingbieOptions = ",".split(',');
			if ('yonghu' == this.tableName) {
				this.rules.lianxidianhua = [{ required: true, validator: this.$validate.isMobile, trigger: 'blur' }];
			}
			if ('yonghu' == this.tableName) {
				this.rules.money = [{ required: true, validator: this.$validate.isNumber, trigger: 'blur' }];
			}
			if ('yuangong' == this.tableName) {
				this.rules.yuangonggonghao = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.yuangonggonghao = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('yuangong' == this.tableName) {
				this.rules.yuangongxingming = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.yuangongxingming = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('yuangong' == this.tableName) {
				this.rules.mima = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.mima = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('yuangong' == this.tableName) {
				this.rules.shoujihao = [{ required: true, validator: this.$validate.isMobile, trigger: 'blur' }];
			}
			if ('yuangong' == this.tableName) {
				this.rules.money = [{ required: true, validator: this.$validate.isNumber, trigger: 'blur' }];
			}
		}
	},
    created() {
		this.pageFlag = this.$route.query.pageFlag;
    },
    //
    methods: {
		changeRules(name){
			if(this.requiredRules[name]){
				return true
			}
			return false
		},
		// uuid
		getUUID () {
			return new Date().getTime();
		},
        // 
		yonghutouxiangUploadChange(fileUrls) {
			this.registerForm.touxiang = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
		},
		yuangongtouxiangUploadChange(fileUrls) {
			this.registerForm.touxiang = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
		},

		// 


		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					var url=this.tableName+"/register";
					if(`yonghu` == this.tableName && this.registerForm.mima!=this.registerForm.mima2) {
						this.$message.error(``);
						return
					}
					if(`yuangong` == this.tableName && this.registerForm.mima!=this.registerForm.mima2) {
						this.$message.error(``);
						return
					}
					this.$http.post(url, this.registerForm).then(res => {
						if (res.data.code === 0) {
							this.$message({
								message: '',
								type: 'success',
								duration: 1500,
								onClose: () => {
									this.$router.push('/login');
								}
							});
						} else {
							this.$message.error(res.data.msg);
						}
					});
				} else {
					return false;
				}
			});
		},
		resetForm(formName) {
			this.$refs[formName].resetFields();
		}
    }
}
</script>

